
import useLocale from '@iorp/react-aid/lib/hooks/useLocale';
import useNamespace from '@iorp/react-aid/lib/hooks/useNamespace';
const Error = ({ error }) => {
    const {locale,L} = useLocale();
    const {data} = useNamespace(); 
 return (
    locale &&   locale.current &&   <div className="container mt-5">
        <div className="row justify-content-center">
          <div className="col-md-8">
            <div className="text-center">
              <h1 className="display-1">{error.code}</h1>
              {/* {error.title && <h2 className="mb-4">{error.title}</h2>} */}
              <p className="lead">{L('error'+error.code)}</p> 
              {/* optionals  */}
              {/* <p className="text-muted">Pathname: {window.location.pathname}</p> */}
              {/* <a href="/" className="btn btn-primary mt-4">Go to Homepage</a> */}
            </div>
          </div>
        </div>
      </div>
    );
  };
   
export default Error;

 