import React, { useState, useEffect ,useContext} from 'react';
  
 
 import  Router from '@iorp/react-aid/lib/components/Router' ; 
import  Namespace from '@iorp/react-aid/lib/components/Namespace' ;
import Locale from '@iorp/react-aid/lib/components/Locale';
import useLocale from '@iorp/react-aid/lib/hooks/useLocale';
 

 import routes from './routes'
import {localeOptions, namespaceOptions } from './extensions/release/config';


import Error from './extensions/release/views/error';




// this is done to provide of serverstrings to the router 
const StringsWrapper = ({children})=>{ 
  const {setStrings} = useLocale();

  useEffect(() => {
   
  // Set client strings
  setStrings({en:require('./extensions/release/strings/en.js').default});
  setStrings({vn:require('./extensions/release/strings/vn.js').default});
  setStrings({es:require('./extensions/release/strings/es.js').default});
  setStrings({eu:require('./extensions/release/strings/eu.js').default});
  setStrings({zh:require('./extensions/release/strings/zh.js').default});
  }, []);

  return <>{children}</>

}
const App = ()=>{ 
 
  
  return  <Namespace options={namespaceOptions}> 
      <Locale options={localeOptions}>
   <StringsWrapper>
    <Router basename={process.env.REACT_APP_BASENAME} routes={routes} ErrorPage={Error}/>  
    </StringsWrapper>
    </Locale>
    </Namespace>
}  

 
export default App;